.link-home {
  text-decoration: none;
  margin: auto 0;
}

.div-logo {
  width:calc(1vw + 35px);
}

.div-logo img {
  width: 100%;
  object-fit: contain;
  transition: scale 0.2s;
}

.div-logo img:hover {
  transition: scale 0.2s;
  transform: scale(1.1);
}

@media screen and (min-device-width: 480px) and (min-device-width: 640px) and (max-width: 768px) {
  .div-logo {
    width: 35px;
  }
}